.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: black;
  height: 100vh;
  width: 100vw;
}

.wrapper {
  padding: 25px;
  display: flex;
  flex-direction: column;
  width: 40%;
  height: auto;
  background: white;
  box-shadow: 0px 8px 8px 0px;
  backdrop-filter: blur (2px);
  border-radius: 5px;
  border: 3px solid darkorchid;
}

.app_image {
  margin: 20px;
  max-height: 400px;
  width: auto;
  border-radius: 2px;
}

.location_input {
  margin-right: 20px;
  height: 30px;
  padding-left: 10px;
}